export default {
    props: {
        // value
        value: {
            type: [Number, String, Array],
            default: ''
        },
        // 
        title: {
            type: [String, Number],
            default: ''
        },
        // slot
        options: {
            type: Array,
            default() {
                return []
            }
        },
        // 
        disabled: {
            type: Boolean,
            default: false
        },
        // 
        height: {
            type: [Number, String],
            default: 'auto'
        },
        // 
        closeOnClickOverlay: {
            type: Boolean,
            default: true
        }
    }
}
