export default {
    props: {
        // 
        activeStyle: {
            type: [String, Object],
            default: () => ({
                color: '#2979ff',
                fontSize: '14px'
            })
        },
        // 
        inactiveStyle: {
            type: [String, Object],
            default: () => ({
                color: '#606266',
                fontSize: '14px'
            })
        },
        // 
        closeOnClickMask: {
            type: Boolean,
            default: true
        },
        // 
        closeOnClickSelf: {
            type: Boolean,
            default: true
        },
        // 
        duration: {
            type: [Number, String],
            default: 300
        },
        // 
        height: {
            type: [Number, String],
            default: 40
        },
        // 
        borderBottom: {
            type: Boolean,
            default: false
        },
        // 
        titleSize: {
            type: [Number, String],
            default: 14
        },
        // 
        borderRadius: {
            type: [Number, String],
            default: 0
        },
        // icon
        menuIcon: {
            type: String,
            default: 'arrow-down'
        },
        // 
        menuIconSize: {
            type: [Number, String],
            default: 14
        }
    }
}
